
@import 'https://fonts.googleapis.com/css?family=Roboto:400,500,700';

@import "scss/demo";

@import "scss/materialize";
@import "scss/my_variables";
@import "scss/header";

/* ========CLEARFIX========*/
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}

/* для IE6-7 */
.clearfix {
  zoom: 1;
}
/*END ===== CLEARFIX========*/

*{
  box-sizing: border-box;
}

body{
  font-family: Roboto, sans-serif;
  background: #fcfcfc;
  margin: 0;
  position: relative;
}

a{
  transition: all .218s ease-in-out;
}

button {
  transition: all .1s ease;
}

input{
  border: none;
}

input{
  outline: none !important;
  border-bottom: 1px solid #eeeeee;
  transition: all .3s ease;

  &.error{
    border-bottom: 1px solid $inputError;
  }

  &:focus, &.active{
    border-bottom: 1px solid $mainBlue;
  }
}

body.overfl-hidden{
  overflow: auto;
}

.no-scroll{
  overflow: hidden;
}

input{
  font-family: Roboto, sans-serif;
}

.only-mobile{
  display: none;
}

.container {
  width: 100%;
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.wrap-table_c-modal{
  padding-top: 4px;
}

.wrap-nav-top{
  background: $mainBlue;
  box-shadow: 0px -18px 59px -2px rgba(0,0,0,0.75);
  position: relative;
  z-index: 1;
}

.scrol-cont{
  overflow-x: auto;
  overflow: -moz-scrollbars-none;
}

ul.nav-top{
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 0;
  font-weight: 500;
  white-space: nowrap;

  li{
    display: inline-block;
    //margin-right: 68px;
    margin-right: 127px;
    &:nth-child(4){
      margin-right: 0 !important;
    }
    &:hover{
      a {
        opacity: 1;
      }
    }
    a{
      font-size: 18px;
      color: white;
      opacity: .5;
      text-decoration: none;
      padding: 0 7px;
      line-height: 45px;
      display: block;
      text-transform: none;
      border-bottom: 3px solid transparent;
      &.active{
        opacity: 1;
        border-bottom: 3px solid #1f648f;
      }
    }
  }
}

.input-overfl{
  position: absolute;
  top:0;
  left: 0;
  bottom: 0;
  right: 0;
  cursor: text;
}

.container::-webkit-scrollbar {
  /* This is the magic bit */
  display: none;
}

.main-wrap{
  background: white;
  padding-top: 40px;
  position: relative;
  height: 100%;
  opacity: 1;
}
