#mob {
  .operators-wrap, .keyboard-tablet-wrap.small-keyboard, .news-wrap{
    will-change: transform, opacity;
    transform: translate3d(0, 0, 0);
  }
}

.main-wrap.no-news{
  .modal-news{
    display: none;
  }

  .wrap-left{
    width: 100%;
  }
  .keyboard-tablet-wrap{
    width: 50%;
  }
  .keyboard-box-wrap{
    margin:auto;
  }
  .operators-wrap{
    margin-right: 50%;
  }
}


.operators-wrap{
  padding-right: 30px;
  margin-right: 380px;
  transition: all .7s ease-in-out;
  will-change: transform, opacity;
}

.descr-input-rules{
  font-size: 11.5px;
  font-style: italic;
  color: #bbbcbe;
  margin-top: -2px;
}

.list-inner-tabel_c{
  display: table-cell;
  vertical-align: middle;
}

.wrap-left{
  float: left;
  width: 68%;
}

ul.operator-list{
  list-style: none;
  padding-left: 0;
  font-size: 19px;
  margin: 0;
  margin-left: 35px;
  li{
    position: relative;

    a:hover, a.active{
      background: #f2f4f5;
      padding-left: 10px;
    }
    &:not(:last-child):after{
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      display: block;
      height: 1px;
      background: $colorLines;
      margin: auto;
    }
    a{
      color: #000;
      text-decoration: none;
      display: table;
      height: 100%;
      width: 100%;
      padding-top: 2px;
      padding-bottom: 5px;
      transition: all .3s ease-in-out;
    }
  }
}

.operator-item{
  color: black;
  text-decoration: none;
}

//  ================= KEYBOARD =================
.keyboard-tablet-wrap{
  float: right;
  transition: all .7s ease-in-out;
}

.keyboard-box-wrap{
  width: 380px;
  padding-top: 20px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 10px;
  position: relative;
  background: white;
  box-shadow: 0px 0px 80px -27px rgba(0,0,0,0.2);
}


.wrap-pay-data{
  text-align: center;
  padding-top: 22px;
  margin-bottom: 15px;
}

.bottom-tooth{
  padding-bottom: 20px;
  position: relative;
  &:after{
    content: "";
    position: absolute;
    bottom: -24px;
    right: 0;
    left: 0;
    height: 28px;
    margin: auto;
    background: url(img/status-bg.png) 0 100% no-repeat;
    background-size: cover;
  }
}

.under-keyboard-logos{
  position: absolute;
  left: 0;
  right: 0;
  border: none;
  height: 1px;
  background: #fafafa;
  margin: 0;
}

.inputs-wrap{
  padding-bottom: 20px;
}

input.number{
  line-height: 50px;
  font-size: 24px;
  position: relative;
  width: 100%;
  padding: 0 30px;
  background: #fafafa ;
  outline: none;
}

.wrap-thru_ccv{
  margin-top: 12px;
  margin-right: -10px;
  margin-left: -10px;
}

.input-wrap{
  padding-left: 10px;
  padding-right: 10px;
  float: left;
  &.thru{
    width: 55.5%;
  }
  &.ccv{
    width: 44.5%;
  }
}

.wrap-sum{
  margin-bottom: 18px;
  float: left;
  width: 47%;
  position: relative;
}

.input-overfl{
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

input {
  &.thru, &.ccv, &.sum{
    text-transform: uppercase;
    line-height: 40px;
    font-size: 18px;
    text-align: center;
    width: 100%;
    padding: 0;
  }

  &.sum{
    text-align: left;
    padding-left: 11px;
    padding-right: 30px;
    font-size: 26px;
  }
}

.wrap-sum{
  position: relative;
}



.btn{
  font-weight: bold;
  font-size: 18px;
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  display: block;
  text-transform: uppercase;
  text-decoration: none;
  color: white;
  background: $mainBlue;
  box-shadow: none !important;
  transition: all .3s ease-in-out;
  &.disable{
    color: $mainBlue;
    background: #eeeeee;
  }
}

.next{
  width: 100%;
  height: auto;
  line-height: 20px;
  &:not(.disable):hover{
    background: darken($mainBlue, 5%);
  }
}

.keyboard-logo{
  float: left;
  text-align: center;
  width: 20%;
  &:hover{
    opacity: .7;
  }
}

.keyboard-logos-wrap{
  margin-bottom: 17px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;

  img{
    vertical-align: middle;
  }
}

.keyboard-wrap{
  padding-top: 12px;
  margin-right: -5px;
  margin-left: -5px;
  position: relative;
}

.help-modal-small{
  text-align: left;
  line-height: 20px;
  z-index: 300;
}

.modal-arrow-relative{
  position: relative;
  height: 100%;
  &:before{

  }
}

.help-modal-small .arrow-dialog{
  padding: 10px;
}

.modal-keyboard{
  top: -50px!important;
  width: 180px;
  right: 5px!important;
  left: auto !important;
  .arrow-dialog:after {
    left: 20px;
    top: auto;
    right: auto;
    bottom: -5px;
  }
}
.modal-menu{
  top: 65px !important;
  width: 165px;
  left: 9px !important;
  .arrow-dialog:after {
    left: 10px;
    top: -4px;
  }
}
.modal-menu-li{
  top: 6px !important;
  left: 230px !important;
  width: 200px;
  .arrow-dialog:after {
    left: -5px;
    top: 13px;
  }
}

.clear-sum{
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  right: 10px;
  width: 14px;
  cursor: pointer;
  background: url("img/clean-sum.png") center no-repeat;
}

.remark{
  font-size: 14px;
  color: #666;
  display: block;
  margin-top: 15px;
  margin-bottom: 20px;
  text-align: left;
  background: none;
  border: none;
  outline: none;
  padding-left: 0;
  text-decoration: underline;
  &:hover{
    text-decoration: none;
  }
  &.check{
    text-decoration: none;
  }
}

.remark-text{
  height:0;
  width: 100%;
  display: block;
  overflow: hidden;
  padding: 0 10px;
  line-height: 50px;
  font-size: 18px;
  position: relative;
  background: #fafafa ;
  opacity: 0;
  border-bottom: 1px solid transparent;
  transition: all .2s ease-in-out;
  &.show{
    height: 50px;
    opacity: 1;
  }
  &:not(.show){
    outline: none;
  }
}

.wrap-status {
  padding-bottom: 24px;
  img{
    height: 30px;
  }
  .status {
    font-size: 18px;
    margin-top: 10px;
  }
}

.status-img{
  height: 30px;
  width: 30px;
  margin:auto;
  position: relative;

  img{
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
  }
}

.next.onkeyboard{
  position: relative;

  .next-onkeyboard-inner{
    position: absolute;
    left: 0;
    top: 0;
    bottom:0;
    width: 0%;
    overflow: hidden;
    //z-index: 2;
    background: $mainBlue;
    line-height: 50px;
    transition: all 2s;
  }

  .next-onkeyboard-overfl{
    position: absolute;
    left: 0;
    top: 0;
    bottom:0;
    right: 0;
  }

  &.allow{
    color: white;
    .next-onkeyboard-inner{
      width: 100%;
      background: green;
    }
  }
}

.anim-arrow{
  -webkit-animation: rotate-arrow 1.4s infinite; /* Safari 4.0 - 8.0 */
  animation: rotate-arrow 1.4s infinite;
  -webkit-animation-timing-function: linear; /* Safari 4.0 - 8.0 */
  animation-timing-function: linear;
}

@-webkit-keyframes rotate-arrow {
  from {transform: rotate(0deg);}
  to {transform: rotate(-360deg);}
}

@keyframes rotate-arrow {
  from {transform: rotate(0deg);}
  to {transform: rotate(-360deg);}
}



.execute-operation{
  margin-top: 35px;
  margin-bottom: 20px;
}

.print{
  border: none;
  background: none;
  padding: 0;
  cursor: pointer;
  position: absolute;
  bottom: 15px;
  right: 23px;
  outline: none;
}

.pay-number{
  font-size: 30px;
  color: $mainBlue;
  margin-top: 7px;
}

.wrap-pay-values{
  font-size: 20px;
  width: 100%;
  max-width: 190px;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 35px;
  line-height: 25px;

  .text{
    float: left;
  }
  .value{
    float: right;
  }
}

.input-wrap-icon{
  position: relative;
  display: none;
  margin-bottom: 20px;

  &.show{
    display: block !important;
  }

  input{
    cursor: text;
  }
}

.from-client .text{
  font-weight: bold;
}

.bkspace-input, .clear-sum{
  top: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  vertical-align: middle;
  cursor: pointer;
  opacity: .7;
  transition: all .2s ease-in-out;

  svg{
    height: 100%;
  }

  &:hover{
    opacity: 1;
  }
}

.reseiver-name{
  text-align: left;
  font-size: 18px;
  margin-bottom: 15px;
}

.key-wrap{
  float: left;
  padding: 0 5px;
  width: 33.333%;
  height: 60px;
  font-size: 30px;
  margin-bottom: 10px;
}

.key-inner{
  font-size: 30px;
  font-weight: 400;
  color: black;
  padding: 0;
  background: #fafafa;
  height: 100%;
  width: 100%;
  text-transform: none;

  &:hover{
    background: $mainBlue;
    color: white;
    svg{
      fill: white !important;
    }
  }

  &.noHover{
    background: #fafafa !important;
    color: black !important;
    svg{
      fill: black !important;
    }
  }
}

.keyboard-wrap.letters{
  margin-left: -2px;
  margin-right: -2px;
  display: none;
  &.uppercased{
   .key-wrap:not(.switch-number, .space, .switch-en, .switch-ru) .key-inner {
     text-transform: uppercase !important;
   }
  }
  .key-wrap{
    height: 50px;
    width: 9.09%;
    padding: 0 2px;
    &.active .key-inner{
      background: $mainBlue;
    }

    .key-inner{
      border: 1px solid #f3f3f3;
      font-size: 22px;
    }
  }
}

.keyboard-wrap.letters.en{
  >*:not(.bottom-letters){
    .key-wrap{
      height: 50px;
      width: 10%;
      padding: 0 2px;
      &.bkspace, &.upper{
        width: 15%;
      }


      .key-inner{
        border: 1px solid #f3f3f3;
        font-size: 22px;
      }
    }
  }
}

.leftspace-mibble{
  margin-left: 17px;
}

.bottom-letters{
  clear: both;
}


.keyboard-wrap.symbols{
  margin-left: -2px;
  margin-right: -2px;
  display: none;
  &.uppercased{
    text-transform: uppercase !important;
  }
  .key-wrap{
    height: 50px;
    width: 12.5%;
    padding: 0 2px;

    .key-inner{
      border: 1px solid #f3f3f3;
      font-size: 18px;

      &.letters{
        font-size: 17px;
      }
    }
  }
}

.svg-keyboard-icon{
  width: 100%;
  height: 19px;
  fill: black;
  display: block;
  transition: all .3s ease-in-out;
}

.key-wrap{
  &.space{
    width: 48.7% !important;
    .key-inner{
      font-size: 14px !important;
    }
  }
  &.switch-numbers{
    width: 12% !important;
    .key-inner{
      font-size: 15px !important;
    }
  }
  &.switch-ru, &.switch-en{
    .key-inner{
      font-size: 19px !important;
    }
  }
  &.switch-symbols{
    width: 12% !important;
    .key-inner{
      font-size: 18px !important;
    }
  }
}

.letters{
  font-size: 23px;
}

//index2
.keyboard-operator-name{
  vertical-align: middle;
  //margin-left: 10px;
}

//index3

// END ========== KEYBOARD =================

// =============== NEWS ======================
.news-wrap{
  display: block;
  float: left;
  width: 32%;
  padding-left: 32px;
  margin-top: -2px;
  transition: all .7s ease-in-out;
}

.news-img{
  width: 100%;
  margin-bottom: 15px;
}

.hide-news{
  display: block;
}

.close-news-modal{
  display: none;
}

.news-item h2{
  margin-top: 0;
  margin-bottom: 27px;
}

.top-name{
  color: $mainBlue;
  font-size: 22px;
  display: block;
  line-height: 21px;
  text-decoration: underline;
  float: left;
  &:hover{
    text-decoration: none;
  }
}

.news-title{
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 18px;
}

.news-text {
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 17px;
  p{
    margin-top: 0;
    margin-bottom: 17px;
  }
}

.under-keyboard-line{
  border: none;
  height: 1px;
  width: 93%;
  background: $colorLines;
  &.only-mobile{
    display: none !important;
  }
}

.news-footer{
  font-size: 14px;
  color: #aaaaaa;
}

.help-relative{
  position: relative;
  height: 100%;
}

.help-modal{
  display: none;
  position: absolute;
  color: black;
  top: 25px;
  left: 60px;
  background: white;
  font-size: 16px;
  font-weight: 400;
  z-index: 200;
  box-shadow: 0 0 17px -3px rgba(0,0,0,.3);
  transition: opacity .3s ease-in-out;
  &.show{
    //display: block;
  }
}

.show-modal{
  &.help{
    display: block !important;
  }
  .help-modal{
    display: block;
  }
}

.deprecate-modal .help-modal{
  display: none !important;
}

.arrow-dialog{
  position: relative;
  padding: 20px;
  &:after{
    content: "";
    position: absolute;
    left: -4px;
    top: 20px;
    width: 8px;
    height: 8px;
    background: #fff;
    transform: rotate(45deg);
  }
}

.help-title{
  font-weight: bold;
  text-align: left;
  margin-bottom: 10px;
}

.wrap-help-number{
  position: relative;
  padding-left: 30px;
  &:before{
    content: '';
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    width: 20px;
    margin:auto;
    background: url("img/phone.png") center no-repeat;
  }
}

.time{
  text-align: left;
  margin-top: 10px;
  &:before{
    content: '';
    background: url("img/clock.png") center no-repeat;
  }
}

.help-numb{
  white-space: nowrap;
  img, span{
    vertical-align: middle;
  }
  img{
    margin-left: 10px;
  }
}

.news-item-inner{
  position: relative;
  //&:not(:last-child){
    padding-bottom: 40px;
  &:not(:last-child){
    &:after{
      content: '';
      position: absolute;
      bottom: 17px;
      width: 100%;
      height: 1px;
      background: $colorLines;
    }
  }
}

.help{
  position: fixed;
  //left: 0;
  bottom: 50px;
  margin-left: -20px;
  margin-top: 20px;
  height: 160px;
  width: 45px;
  text-align: center;
  word-wrap: break-word;
  border: 1px solid #ebebeb;
  border-left: none;
  z-index: 10;
  transition: all .4s .3s ease-in-out;

  .help-inner{
    position: relative;
    padding: 45px 17px 10px;
    line-height: 16px;
    cursor: pointer;
    height: 100%;
    background: white;
    &:hover{
      background: #fafafa;
    }

    &:before{
      content: "";
      position: absolute;
      display: block;
      left: 0;
      right: 0;
      margin: auto;
      top: 20px;
      width: 20px;
      height: 20px;
      background: url(img/help.png) no-repeat;
    }
  }
}

@import "scss/media";


