    padding-top: 5px; }
  .wrap-table_c-modal {
    padding-top: 10px; }
  .remark {
    margin-top: 10px;
    margin-bottom: 15px; }
  .remark-text.show {
    height: 40px; }
  .keyboard-box-wrap {
    padding-top: 15px; }
  input.number {
    line-height: 43px;
    font-size: 22px; }
  .keyboard-logos-wrap {
    margin-bottom: 12px;
    font-size: 17px; }
  input.ccv, input.sum, input.thru {
    line-height: 35px;
    font-size: 24px; }
  .key-wrap {
    height: 50px;
    margin-bottom: 6px; }
  .key-inner:not(.letters) {
    font-size: 25px; } }
