#mob-rule {
  .operators-wrap { display: block      !important; }
  #wrap-nav-icon  { display: table-cell !important; }
  .back           { display: none       !important; }


  &.small-keyboard {
    #mob .small-keyboard { display: block !important; }
    #mob .check-begin    { display: none  !important; }
    #mob .check-process  { display: none  !important; }
    #mob .check-success  { display: none  !important; }
    #mob .check-error    { display: none  !important; }
  }


  &:not(.small-keyboard) {
    #mob .small-keyboard { display: none !important; }
    #mob .check-begin    { display: none !important; }
    #mob .check-process  { display: none !important; }
    #mob .check-success  { display: none !important; }
    #mob .check-error    { display: none !important; }

    &.check-begin   { #mob .check-begin   { display: block  !important; }}
    &.check-process { #mob .check-process { display: block  !important; }}
    &.check-success { #mob .check-success { display: block  !important; }}
    &.check-error   { #mob .check-error   { display: block  !important; }}
  }
}

