// =============================== 750px =================================
@media screen and (max-width: 750px) {
  #mob-rule, #internet-rule, #pay-systems-rule, #web-services-rule {
    &:not(.small-keyboard){
      #wrap-nav-icon { display: none  !important; }
      .operators-wrap{ display: none  !important; }
      .back          { display: block !important; }
      .wrap-nav-top  { display: none  !important; }
      .keyboard-box-wrap{ margin-top: 0 !important;}
    }
  }
}
// END =========================== 750px ================================
